import styles from "./UserList.module.css";
import Card from "../UI/Card";
import User from "./User";
const UserList = (props) => {
  return (
    <Card className={styles.list}>
      {props.list.length !== 0
        ? props.list.map((arr) => {
            return <User key={arr.id} item={arr} />;
          })
        : "No Users"}
    </Card>
  );
};
export default UserList;
